import { Avatar, Backdrop, Button, Card, CardActions, CardContent, CardHeader, CardMedia, CircularProgress, Container, Grid, IconButton, InputBase, Paper, Typography } from "@mui/material";
import React from "react";
import PrintIcon from '@mui/icons-material/Print';
import SearchIcon from '@mui/icons-material/Search';
import EditIcon from '@mui/icons-material/Edit';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import { format } from "date-fns";
import dateFormat from "dateformat";
import { Link } from "react-router-dom";

class Preventivi extends React.Component {

    constructor(props) {
        super(props)
        this.state = {
            preventivi: [],
            searchText: '',
            openLoader: false
        }
    }

    componentDidMount() {
        this.getPreventivi()
    }

    getPreventivi = () => {
        const myHeaders = new Headers({
            'Content-Type': 'application/json',
            'Authorization': 'EMQHS3K0YUIMXSR0O6JA'
          });

          const requestOptions = {
            method: "GET",
            headers: myHeaders
        };
        fetch(window.location.protocol + '//' + window.location.host + "/api/preventivi/", requestOptions)
        .then(async response => {
            const isJson = response.headers.get('content-type')?.includes('application/json');
            const data = isJson && await response.json();

            // check for error response
            if (!response.ok) {
                // get error message from body or default to response status
                const error = (data && data.message) || response.status;
                return Promise.reject(error);
            }
            

            this.setState({
                preventivi: data
            })
        })
        .catch(error => {

            console.log("Errore:"+error)

            

            
        });
    }

    searchPreventivi = () => {

        this.setState({
            openLoader: true
        })

        const myHeaders = new Headers({
            'Content-Type': 'application/json',
            'Authorization': 'EMQHS3K0YUIMXSR0O6JA'
          });

          const requestOptions = {
            method: "GET",
            headers: myHeaders
        };
        fetch(window.location.protocol + '//' + window.location.host + "/api/preventivi/?search="+this.state.searchText, requestOptions)
        .then(async response => {
            const isJson = response.headers.get('content-type')?.includes('application/json');
            const data = isJson && await response.json();

            // check for error response
            if (!response.ok) {
                // get error message from body or default to response status
                const error = (data && data.message) || response.status;
                return Promise.reject(error);
            }

            this.setState({
                preventivi: data,
                openLoader: false
            })
        })
        .catch(error => {

            console.log("Errore:"+error)

            

            
        });
    }

    render() {
        return (
            <div>
                <Grid container spacing={2} padding={5}>
                    <Grid xs={0} md={2} item />
                    <Grid xs={12} md={8} item >
                    <Paper sx={{ p: '2px 4px', display: 'flex', alignItems: 'center' }}>
                        <InputBase 
                            sx={{ ml: 1, flex: 1 }}
                            onChange={(e) => this.setState({searchText: e.target.value})}
                            placeholder="Cerca per nome, cognome, oggetto..."
                            inputProps={{ 'aria-label': 'search google maps' }}
                        />
                        <IconButton type="button" sx={{ p: '10px' }} aria-label="search" onClick={this.searchPreventivi}>
                            <SearchIcon />
                        </IconButton>
                    </Paper>
                    </Grid>
                    {this.state.preventivi.map((prev, key) => (
                    <Grid item xs={12} md={3} >
                    <Card sx={{ maxWidth: 345 }}>
                        <CardHeader
                            avatar={
                            <Avatar sx={{ bgcolor: "#1976d2" }} aria-label="recipe">
                                {Array.from(prev.datiCliente.nome)[0]+Array.from(prev.datiCliente.cognome)[0]}
                            </Avatar>
                            }
                            title={prev.datiCliente.nome + " " + prev.datiCliente.cognome}
                            subheader={dateFormat(prev.datiPreventivo.data, "dd/mm/yyyy")}
                        />
                        <CardContent>
                            <Typography variant="body2" color="text.secondary">
                            {prev.datiPreventivo.oggetto}
                            </Typography>
                        </CardContent>
                        <CardActions disableSpacing>
                            <IconButton aria-label="edit">
                                <Link to={"/dettaglio/"+prev.id} style={{color: "rgba(0, 0, 0, 0.54)", textDecoration: "none"}}><EditIcon /></Link>
                            </IconButton>
                            <IconButton target='_blank' href={window.location.protocol + '//' + window.location.host +"/api/preventivi/"+prev.id+"/stampa"} aria-label="add to favorites">
                                <PrintIcon />
                            </IconButton>
                            
                        </CardActions>
                        </Card>
                    </Grid>
                    ))}

                <Backdrop sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }} open={this.state.openLoader}>
                    <CircularProgress color="inherit" />
                </Backdrop>
                </Grid>
            </div>
        )
    }

}
export default Preventivi;