import { alpha, Checkbox, IconButton, Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Toolbar, Tooltip, Typography } from "@mui/material";
import React from "react";
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import { SortableContainer , SortableHandle , SortableElement , arrayMove } from  'react-sortable-hoc';

    const TAX_RATE = 0.22;

    function subtotal(items) {
      return items.map(({ price }) => price).reduce((sum, i) => sum + i, 0);
    }
      
    function ccyFormat(num) {
        return Number.parseFloat(num).toFixed(2).replace(".", ",");
    }

    var i = 0;

    export default function Prodotti(props) {

      const invoiceSubtotal = subtotal(props.rows);
      const invoiceTaxes = TAX_RATE * invoiceSubtotal;
      const invoiceTotal = invoiceTaxes + invoiceSubtotal;

      const onSortEnd  = ({oldIndex, newIndex}) => {
          const newArray = arrayMove(props.rows, oldIndex, newIndex)
          var arr = []

          newArray.map((prod, index) => {
            arr.push({
              id: index+1,
              desc: prod.desc,
              price: prod.price,
              qty: prod.qty,
              um: prod.um,
              unit: prod.unit
            })
          })

          props.setRows(arr)
      };

      const  DragHandle = SortableHandle(({style}) => (
        <span style={{ ...style, ...{cursor : 'move' }}}> { '::::' } </span>)
      )


      const isSelected = (id) => props.selected.indexOf(id) !== -1;

      function getUm(um) {
        if(um === "ac") {
          return "A corpo";
        }
        if(um === "mq") {
          return "Metri quadrati";
        }
        if(um === "ml") {
          return "Metri lineari";
        }
      }

      const numSelected = props.selected.length;
      const rowCount = props.rows.length;

      const SortableItem = SortableElement(({row, ...other}) => {
        return(
        <TableRow {...other}>
          {other.children}
          <TableCell style={{width :  '5%' }} >
            <DragHandle/>
          </TableCell>
          <TableCell padding="checkbox">
            <Checkbox
              color="primary"
              checked={other.isItemSelected}
              onClick={(e) => props.handleClick(e, row.id)}
              inputProps={{
                'aria-labelledby': other.labelId,
              }}
            />
          </TableCell>
          <TableCell>{row.id}</TableCell>
          <TableCell>{row.desc}</TableCell>
          <TableCell>{getUm(row.um)}</TableCell>
          <TableCell align="right">{ccyFormat(row.qty)}</TableCell>
          <TableCell align="right">{ccyFormat(row.unit)}</TableCell>
          <TableCell align="right">{ccyFormat(row.price)}</TableCell>
        </TableRow >)
      });

      const  TableBodySortable  =  SortableContainer(({children, displayRowCheckbox}) => (
        <TableBody displayRowCheckbox={displayRowCheckbox} >
          {children}
        </TableBody >
      ))

      TableBodySortable.muiName  =  'TableBody'

        return (
          <div>
          <Toolbar
              sx={{
                pl: { sm: 2 },
                pr: { xs: 1, sm: 1 },
                ...(numSelected > 0 && {
                  bgcolor: (theme) =>
                    alpha(theme.palette.primary.main, theme.palette.action.activatedOpacity),
                }),
              }}
            >
            {numSelected > 0 ? (
                <Typography
                  sx={{ flex: '1 1 100%' }}
                  color="inherit"
                  variant="subtitle1"
                  component="div"
                >
                  {numSelected} selezionato/i
                </Typography>
              ) : (
                <Typography
                  sx={{ flex: '1 1 100%' }}
                  variant="h6"
                  id="tableTitle"
                  component="div"
                >
                  Dettaglio prodotti
                </Typography>
              )}

              {numSelected === 1 ? (
                <Tooltip title="Modifica selezionato">
                  <IconButton onClick={props.editSelected}>
                    <EditIcon />
                  </IconButton>
                </Tooltip>
              ) : (
                <div></div>
              )}

              {numSelected > 0 ? (
                <Tooltip title="Cancella selezionati">
                  <IconButton onClick={props.deleteSelected}>
                    <DeleteIcon />
                  </IconButton>
                </Tooltip>
              ) : (
                <div></div>
              )}
            </Toolbar>
            <TableContainer component={Paper}>
              <Table sx={{ minWidth: 700 }} aria-label="spanning table">
                <TableHead>
                  <TableRow>
                  <TableCell style={{width :  '5%' }} > </TableCell>
                    <TableCell padding="checkbox">
                      <Checkbox
                        color="primary"
                        indeterminate={numSelected > 0 && numSelected < rowCount}
                        checked={rowCount > 0 && numSelected === rowCount}
                        onChange={props.handleSelectAllClick}
                        inputProps={{
                          'aria-label': 'select all desserts',
                        }}
                      />
                    </TableCell>

                    <TableCell>#</TableCell>
                    <TableCell width={900}>Descrizione</TableCell>
                    <TableCell>UM</TableCell>
                    <TableCell align="right">Qt.</TableCell>
                    <TableCell align="right">Pr. Un.</TableCell>
                    <TableCell align="right">Totale</TableCell>
                  </TableRow>
                </TableHead>
                <TableBodySortable onSortEnd = {onSortEnd} useDragHandle displayRowCheckbox={false}>
                  {props.rows.map((row, index) => {
                    const isItemSelected = isSelected(row.id);
                    const labelId = 'enhanced-table-checkbox-'+row.id;
                      return (
                        <SortableItem row={row} index={index} isItemSelected={isItemSelected} labelId={labelId}/>
                      )
                  })
                  }
                  <TableRow>
                    <TableCell rowSpan={3} colSpan={4} />
                    <TableCell colSpan={3} >Imponibile</TableCell>
                    <TableCell align="right">{ccyFormat(invoiceSubtotal)}</TableCell>
                  </TableRow>
                </TableBodySortable>
              </Table>
            </TableContainer>
            </div>
          );
    }