import { Backdrop, Box, Button, CircularProgress, FormControl, Grid, Modal, OutlinedInput, TextField, Typography } from "@mui/material";
import { useState } from "react";
import Toast from "./Toast";

export default function InviaMail(props) {

    const [openAlert, setOpenAlert] = useState(false);
    const [textAlert, setTextAlert] = useState("");
    const [severity, setSeverity] = useState("");
    const [mail, setMail] = useState("");
    const [loader, setLoader] = useState(false);

    const style = {
        position: 'absolute',
        top: '50%',
        left: '50%',
        width: '90%',
        transform: 'translate(-50%, -50%)',
        bgcolor: 'background.paper',
        border: '2px solid #000',
        boxShadow: 24,
        p: 4,
    }

    function closeToast() {
        setOpenAlert(false)
    }


    function setToast(text, severity) {
            setTextAlert(text)
            setSeverity(severity)
    }

    function handleSubmit() {

        setLoader(true)

        const myHeaders = new Headers({
            'Content-Type': 'application/json',
            'Authorization': 'EMQHS3K0YUIMXSR0O6JA'
          });

          const requestOptions = {
            method: "GET",
            headers: myHeaders
        };
        fetch(window.location.protocol + '//' + window.location.host +"/api/preventivi/"+props.getId+"/mail?mail="+mail, requestOptions)
        .then(async response => {
            const isJson = response.headers.get('content-type')?.includes('application/json');
            const data = isJson && await response.json();

            // check for error response
            if (!response.ok) {
                // get error message from body or default to response status
                const error = (data && data.message) || response.status;
                return Promise.reject(error);
            }

            setLoader(false)

            setToast("Mail inviata correttamente!", "success")
            setOpenAlert(true)

        })
        .catch(error => {

            setToast("Si è verificato un errore durante l'invio della mail. Riprova", "error")
            setOpenAlert(true)
        });
    }

    return (
    <Modal
        open={props.openMail}
        onClose={props.closeMail}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
        >
            <div>
            <Box sx={style}>
                <Typography id="modal-modal-title" variant="h6" component="h2" align="center">
                Inserisci l'indirizzo mail del cliente
                </Typography>
                <Grid container spacing={2} padding={5}>
                    <Grid xs={12} md={9} item>                   
                        <TextField fullWidth
                            id="mail"
                            name="mail"
                            onChange={(e) => setMail(e.target.value)}
                            label="Indirizzo mail"
                        />
                    </Grid>
                    <Grid xs={12} md={3} item>                   
                        <Box textAlign='center' sx={{mt:1}}>
                            <Button variant='contained' type="submit" onClick={handleSubmit}>
                                Invia mail
                            </Button>
                        </Box>
                    </Grid>
                </Grid>
            </Box>
            <Toast open={openAlert} close={() => closeToast} text={textAlert} severity={severity}></Toast>
            <Backdrop sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }} open={loader}>
                <CircularProgress color="inherit" />
            </Backdrop>
            </div>
        </Modal>
        )
}