import { AppBar, Box, Button, IconButton, Toolbar, Typography } from "@mui/material"
import React from "react"
import { Link } from "react-router-dom"

class CustomNavbar extends React.Component {
    

    render() {
        return (
            <Box sx={{ flexGrow: 1, mb:3 }}>
                <AppBar position="static">
                    <Toolbar>
                        <Typography variant="h6" component="div" sx={{ flexGrow: 1, left:0 }}>
                            Edilre Preventivatore
                        </Typography>
                        <Button color="inherit"><Link to="/" style={{color: "#FFF", textDecoration: "none"}}>Nuovo</Link></Button>
                        <Button color="inherit"><Link to="/storico" style={{color: "#FFF", textDecoration: "none"}}>Storico</Link></Button>
                    </Toolbar>
                </AppBar>
            </Box>
        )
    }

}

export default CustomNavbar