import { Backdrop, CircularProgress, Fab, Input } from "@mui/material";
import React, { useRef } from "react";
import Cliente from "./Cliente";
import TabellaProdotti from "./TabellaProdotti";
import Pulsantiera from "./Pulsantiera";
import ConfermaPreventivo from "./ConfermaPreventivo";
import ToastAlert from "./Toast";
import Toast from "./Toast";
import { useParams } from "react-router-dom";

class Nuovo extends React.Component {

    constructor(props) {
        super(props)

        const id = props.id ? props.id : 0

        this.state = {
            rows: [],
            openConferma: false,
            id: id,
            openAlert: false,
            textAlert: '',
            severity: '',
            openLoader: false,
            disabled: true,
            prodotti: [],
            selected: [],
            nuovo:true,
        }

        if(this.state.id !== 0) {
            this.caricaPreventivo()
        }

    }

    componentDidMount() {



        this.uploadProdotti()
    }

    uploadProdotti = () => {
        const myHeaders = new Headers({
            'Content-Type': 'application/json',
            'Authorization': 'EMQHS3K0YUIMXSR0O6JA'
          });

          const requestOptions = {
            method: "GET",
            headers: myHeaders
        };
        fetch(window.location.protocol + '//' + window.location.host + "/api/prodotti/", requestOptions)
        .then(async response => {
            const isJson = response.headers.get('content-type')?.includes('application/json');
            const data = isJson && await response.json();

            // check for error response
            if (!response.ok) {
                // get error message from body or default to response status
                const error = (data && data.message) || response.status;
                return Promise.reject(error);
            }
            

            this.setState({
                prodotti: data
            })
        })
        .catch(error => {

            this.setToast("Si è verificato un errore durante il recupero dei prodotto. Ricarica la pagina", "error")
            this.openAlert()

            
        });
    }

    caricaPreventivo = () => {
        const myHeaders = new Headers({
            'Content-Type': 'application/json',
            'Authorization': 'EMQHS3K0YUIMXSR0O6JA'
          });

          const requestOptions = {
            method: "GET",
            headers: myHeaders
        };
        fetch(window.location.protocol + '//' + window.location.host + "/api/preventivi/"+this.state.id, requestOptions)
        .then(async response => {
            const isJson = response.headers.get('content-type')?.includes('application/json');
            const data = isJson && await response.json();

            // check for error response
            if (!response.ok) {
                // get error message from body or default to response status
                const error = (data && data.message) || response.status;
                return Promise.reject(error);
            }

            let arr = []

            data.datiPreventivo.prodotti.map((prod, index) => {
                arr.push({
                    id: index+1,
                    desc: prod.desc,
                    price: prod.price,
                    qty: prod.qty,
                    um: prod.um,
                    unit: prod.unit
                })
            })

            this.setState({
                rows: arr,
                datiCliente: {
                    nome:data.datiCliente.nome,
                    cognome:data.datiCliente.cognome,
                    indirizzo:data.datiCliente.indirizzo
                },
                datiPreventivo: {
                    oggetto:data.datiPreventivo.oggetto,
                    data: data.datiPreventivo.data
                },
                disabled:false,
                nuovo:false
            })
        })
        .catch(error => {

            this.setToast("Si è verificato un errore durante il recupero dei preventivo. Ricarica la pagina", "error")
            this.openAlert()

            
        });
    }

    handleClick = (event, name) => {

        

        const selectedIndex = this.state.selected.indexOf(name);
        let newSelected = [];
    
        if (selectedIndex === -1) {
            newSelected = newSelected.concat(this.state.selected, name);
        } else if (selectedIndex === 0) {
            newSelected = newSelected.concat(this.state.selected.slice(1));
        } else if (selectedIndex === this.state.selected.length - 1) {
            newSelected = newSelected.concat(this.state.selected.slice(0, -1));
        } else if (selectedIndex > 0) {
            newSelected = newSelected.concat(
                this.state.selected.slice(0, selectedIndex),
                this.state.selected.slice(selectedIndex + 1),
            );
        }   
        this.setState({
            selected: newSelected
        })
    };

    handleSelectAllClick = (event) => {
        if (event.target.checked) {
          const newSelected = this.state.rows.map((n) => n.id);
          this.setState({
            selected: newSelected
            })
          return;
        }
        this.setState({
            selected: []
        })
    };

    deleteSelected = () => {
        const newArr = this.state.rows.filter((row) => !this.state.selected.includes(row.id))
        newArr.map((prod, i) => prod.id = i+1)

        this.setState({
            rows:newArr
        })

        this.setState({
            selected: []
        })


    }


    getDatiCliente = () => {
        return this.state.datiCliente
    }

    getDatiPreventivo = () => {
        return this.state.datiPreventivo
    }

    closeMail = () => {
        this.setState({
            openMail: false
        })
    }

    openConferma = () => {
        this.setState({
            openConferma: true
        })
    }

    closeConferma = () => {
        this.setState({
            openConferma: false
        })
    }

    closeAlert = () => {
        this.setState({
            openAlert: false
        })
    }

    openAlert = () => {
        this.setState({
            openAlert: true
        })
    }

    getDisabled = () => {
        return this.state.disabled;
    }

    setToast = (text, severity) => {
        this.setState({
            textAlert: text,
            severity: severity
        })
    }

    handleSubmit = (e) => {

        e.preventDefault();
        

        this.setState({
            openLoader: true
        })

        if(this.state.rows.length === 0) {
            this.setToast("Non è possibile creare un preventivo senza prodotti", "error")
            this.openAlert()

            this.setState({
                openLoader: false
            })
        } else {
            const target = e.target;



            const body = {
                datiCliente: {
                    nome: target.nome.value,
                    cognome: target.cognome.value,
                    indirizzo: target.indirizzo.value
                },
                datiPreventivo: {
                    oggetto: target.oggetto.value,
                    prodotti: this.state.rows,
                    data: target.data.value
                }
                
            }
    
            const myHeaders = new Headers({
                'Content-Type': 'application/json',
                'Authorization': 'EMQHS3K0YUIMXSR0O6JA'
              });


            if(this.state.id == 0) {

                const requestOptions = {
                    method: "POST",
                    headers: myHeaders,
                    body: JSON.stringify(body)
                };
                fetch(window.location.protocol + '//' + window.location.host +"/api/preventivi/", requestOptions)
                .then(async response => {
                    const isJson = response.headers.get('content-type')?.includes('application/json');
                    const data = isJson && await response.json();
        
                    // check for error response
                    if (!response.ok) {
                        // get error message from body or default to response status
                        const error = (data && data.message) || response.status;
                        return Promise.reject(error);
                    }
        
                    this.setState({
                        id: data.id,
                        disabled: false,
                        openLoader: false
                    })


                    this.openConferma()
                })
                .catch(error => {

                    this.setState({
                        openLoader: false
                    })

                    this.setToast("Si è verificato un errore in fase di salvataggio. Riprova", "error")
                    this.openAlert()
                });

            } else {

                const requestOptions = {
                    method: "PUT",
                    headers: myHeaders,
                    body: JSON.stringify(body)
                };
                fetch(window.location.protocol + '//' + window.location.host +"/api/preventivi/"+this.state.id+"/", requestOptions)
                .then(async response => {
                    const isJson = response.headers.get('content-type')?.includes('application/json');
                    const data = isJson && await response.json();
        
                    // check for error response
                    if (!response.ok) {
                        // get error message from body or default to response status
                        const error = (data && data.message) || response.status;
                        return Promise.reject(error);
                    }

                    this.setState({
                        openLoader: false
                    })
        
                    this.setToast("Preventivo aggiornato con successo", "success")
                    this.openAlert()
                })
                .catch(error => {

                    this.setState({
                        openLoader: false
                    })

                    this.setToast("Si è verificato un errore in fase di salvataggio. Riprova", "error")
                    this.openAlert()
                });
            }


    
            
        }

        
    }

    setRows = (newArr) => {
        this.setState({
            rows: newArr
        })
    }

    getRows = () => {
        return this.state.rows
    }

    setOggetto = (value) => {
        this.setState({
            datiPreventivo: {
                oggetto: value,
                data: this.state.datiPreventivo.data
            }
        })
    }

    setNome = (value) => {
        this.setState({
            datiCliente: {
                nome: value,
                cognome: this.state.datiCliente.cognome,
                indirizzo: this.state.datiCliente.indirizzo
            }
        })
    }

    setCognome = (value) => {
        this.setState({
            datiCliente: {
                nome: this.state.datiCliente.nome,
                cognome: value,
                indirizzo: this.state.datiCliente.indirizzo
            }
        })
    }

    setIndirizzo = (value) => {
        this.setState({
            datiCliente: {
                nome: this.state.datiCliente.nome,
                cognome: this.state.datiCliente.cognome,
                indirizzo: value
            }
        })
    }

    getNuovo = () => {
        return this.state.nuovo;
    }

    render() {
        return (
            <div>
                <form onSubmit={this.handleSubmit}>
                    <Cliente datiCliente={this.getDatiCliente()} setNome={this.setNome} setCognome={this.setCognome} setIndirizzo={this.setIndirizzo}></Cliente>
                    <TabellaProdotti datiPreventivo={this.getDatiPreventivo()} setOggetto={this.setOggetto} deleteSelected={this.deleteSelected} selected={this.state.selected} getRows={this.getRows} setRows={this.setRows} prodotti={this.state.prodotti} handleClick={this.handleClick} handleSelectAllClick={this.handleSelectAllClick}></TabellaProdotti>
                    <Pulsantiera nuovo={this.getNuovo()} disabled={this.state.disabled} getId={this.state.id}></Pulsantiera>
                    <ConfermaPreventivo open={this.state.openConferma} close={this.closeConferma} getId={this.state.id}></ConfermaPreventivo>
                </form>
                <Toast open={this.state.openAlert} close={this.closeAlert} text={this.state.textAlert} severity={this.state.severity}></Toast>
                <Backdrop sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }} open={this.state.openLoader}>
                    <CircularProgress color="inherit" />
                </Backdrop>
            </div>
            
            
        );
    }
}
export default Nuovo;