import { Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle } from "@mui/material";
import React from "react";

export default function ConfermaNuovo(props) {
    const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  function prosegui() {
    window.location.reload()

  }

  return (
    <div>
      <Dialog
        open={props.open}
        onClose={props.close}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          {"Confermi di voler creare un nuovo preventivo?"}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Confermando la scelta, tutti i dati non salvati in questo preventivo andranno persi. Sei sicuro di voler proseguire?
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={props.close}>Annulla</Button>
          <Button onClick={prosegui} autoFocus>
            Prosegui
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}