import { Grid, TextField, Typography } from "@mui/material";
import { th } from "date-fns/locale";
import React from "react";
import NuovoProdotto from "./NuovoProdotto";
import Prodotti from "./Prodotti.js";



class TabellaProdotti extends React.Component {

    constructor(props) {
        super(props);      
        this.state={
            open:false,
            editing:false,
            desc: null,
            um: '',
            qt: null,
            prezzo: null,
            id: null
        }  
    }

    setId = (text) => {
        this.setState({
            id:text
        })
    }

    setDesc = (text) => {
        this.setState({
            desc:text
        })
    }

    setEditing = (bool) => {
        this.setState({
            editing:bool
        })
    }

    setUm = (text) => {
        this.setState({
            um:text
        })
    }

    setQt = (text) => {
        this.setState({
            qt:text
        })
    }

    setPrezzo = (text) => {
        this.setState({
            prezzo:text
        })
    }

    priceRow(qty, unit) {
        return qty * unit;
    }

    incrementCounter() {
        this.id++;
        return this.id;
    }

    addRow = (index, desc, um, qty, unit) => {
        this.createRow(index, desc, um, qty, unit);
    }

    createRow(index, desc, um, qty, unit) {
        console.log(index, desc, um, unit, qty)
        const price = this.priceRow(qty, unit);
        var newArr = this.props.getRows();
        newArr.push({
            desc: desc,
            um: um,
            qty: qty,
            unit: unit,
            price: price
        });

        newArr.map((prod, i) => prod.id = i+1)

        this.props.setRows(newArr)
    }

    modifyRow = (index, desc, um, qty, unit) => {
        const price = this.priceRow(qty, unit);
        var newArr = [];

        this.props.getRows().map((prod, ind) => {
            if(prod.id === index) {
                newArr.push({
                    id: index,
                    desc: desc,
                    um: um,
                    qty: qty,
                    unit: unit,
                    price: price
                });
            } else {
                newArr.push(prod)
            }
        })

        this.props.setRows(newArr)
    }

    editSelected = () => {

        const prod = this.props.getRows().filter(pr => pr.id===this.props.selected[0])


        this.setState({
            open:true,
            id: prod[0].id,
            desc: prod[0].desc,
            um: prod[0].um,
            qt: prod[0].qty,
            prezzo: prod[0].unit,
            editing:true
        })
    }

    handleOpen = () => {
        this.setState({
            open:true
        })
    }

    handleClose = () => {
        this.setState({
            open:false
        })
    }

    render() {
        return (
            <Grid container spacing={2} padding={5}>
                
                <Grid xs={12} item>
                    <Typography sx={{textAlign: "center" }} variant="h5" color="text.secondary" gutterBottom>
                        Dati Preventivo
                    </Typography>
                    
                </Grid>
                <Grid xs={12} item>     
                    <input type="hidden" id="data" value={this.props.datiPreventivo ? this.props.datiPreventivo.data : null} />              
                    <TextField focused={this.props.datiPreventivo ? true : false} sx={{mb: 2}} fullWidth id="oggetto" value={this.props.datiPreventivo ? this.props.datiPreventivo.oggetto : null} onChange={(e) => this.props.setOggetto(e.target.value)} label="Oggetto" variant="standard" required/>
                </Grid>
                <Grid id="prodotti" xs={12} item>
                    <Prodotti setRows={this.props.setRows} editSelected={this.editSelected} deleteSelected={this.props.deleteSelected} selected={this.props.selected} rows={this.props.getRows()} handleClick={this.props.handleClick} handleSelectAllClick={this.props.handleSelectAllClick}/>
                </Grid>
                <NuovoProdotto setEditing={this.setEditing} editing={this.state.editing} setId={this.setId} setDesc={this.setDesc} setUm={this.setUm} setQt={this.setQt} setPrezzo={this.setPrezzo} id={this.state.id} desc={this.state.desc} um={this.state.um} qt={this.state.qt} prezzo={this.state.prezzo} handleOpen={this.handleOpen} handleClose={this.handleClose} open={this.state.open} selected={this.props.selected} index={this.props.getRows().length+1} createRow={this.addRow} modifyRow={this.modifyRow} risultati={this.props.prodotti}/>  
            </Grid>
        );
    }
}

export default TabellaProdotti;