import { Typography, Button, Dialog, DialogTitle, DialogContent } from "@mui/material";
import React, { useState } from "react";

export default function ConfermaPreventivo(props){

  
    return (
        <Dialog onClose={props.close} open={props.open}>
            <DialogTitle>Conferma preventivo</DialogTitle>
            <DialogContent>Ti confermiamo che è stato creato con successo il preventivo numero <b>PRE{String(props.getId).padStart(6, '0')}</b></DialogContent>
        </Dialog>
        );

  }