import { Box, Modal, Typography, Fab, Grid, TextField, InputAdornment, InputLabel, FormControl, OutlinedInput, Select, MenuItem, Button } from "@mui/material";
import AddIcon from '@mui/icons-material/Add';
import Autocomplete from '@mui/material/Autocomplete';
import CircularProgress from '@mui/material/CircularProgress';
import React, { useState } from "react";
import Searchbar from "./Searchbar";
import Prodotti from "./Prodotti.js";
import TabellaProdotti from "./TabellaProdotti";

export default function NuovoProdotto(props) {


    const [openOptions, setOpenOptions] = React.useState(true);
    const [options, setOptions] = React.useState([]);
    const loading = props.open && options.length === 0;

 

    const style = {
        position: 'absolute',
        top: '50%',
        left: '50%',
        width: '90%',
        transform: 'translate(-50%, -50%)',
        bgcolor: 'background.paper',
        border: '2px solid #000',
        boxShadow: 24,
        p: 4,
    }

    const fabStyle = {
        margin: 0,
        top: 'auto',
        right: 20,
        bottom: 20,
        left: 'auto',
        position: 'fixed'
    };

    function findById(array, descrizione) {
        return array.find((element) => {
          return element.descrizione === descrizione;
        })
    }

    function reset() {
        props.setDesc('');
        props.setUm('');
        props.setQt(null);
        props.setPrezzo(null);
    }

    function openModal() {
        props.setEditing(false)
        reset()
        props.handleOpen()
    }

    function isUndefined(text) {
        return text == "undefined" || !text || text == "";
    }

    function handleSubmit() {
        if(isUndefined(props.desc) || isUndefined(props.um) || isUndefined(props.qt) || isUndefined(props.prezzo)) {
            alert("I campi sono obbligatori")
        } else {
            const index = props.index;
            props.createRow(index, props.desc, props.um, props.qt, props.prezzo);
            props.handleClose()
            reset();
        }
        
        
    }

    function handleModify() {
        if(isUndefined(props.desc) || isUndefined(props.um) || isUndefined(props.qt) || isUndefined(props.prezzo)) {
            alert("I campi sono obbligatori")
        } else {
            const index = props.id;
            props.modifyRow(index, props.desc, props.um, props.qt, props.prezzo);
            props.handleClose()
            reset();
        }
        
        
    }


        return (
            <form id="newProduct">
                <Fab onClick={openModal} sx={fabStyle} color="primary" aria-label="add">
                    <AddIcon />
                </Fab>
                <Modal
                open={props.open}
                onClose={props.handleClose}
                aria-labelledby="modal-modal-title"
                aria-describedby="modal-modal-description"
                >
                    <Box sx={style}>
                        { props.editing ?
                        <Typography id="modal-modal-title" variant="h6" component="h2" align="center">
                        Modifica prodotto
                        </Typography>
                        :
                        <Typography id="modal-modal-title" variant="h6" component="h2" align="center">
                        Aggiungi un nuovo prodotto
                        </Typography>
                        }
                        <Grid container spacing={2} padding={5}>
                            <Grid xs={12} item>                   
                            <Autocomplete
                                value={props.desc}
                                freeSolo
                                disableClearable
                                autoComplete
                                onChange={(event, newValue) => {
                                    if (typeof newValue === 'string') {
                                        let elem = findById(props.risultati, newValue);
                                        props.setDesc(elem.descrizione);
                                        props.setUm(elem.um);
                                    } else if (newValue && newValue.inputValue) {
                                      // Create a new value from the user input
                                      props.setDesc(newValue.inputValue);
                                    } else {
                                      props.setDesc(newValue);
                                    }
                                  }}
                                options={props.risultati.map((option) => option.descrizione)}
                                filterOptions={(options, state) => {
                                    if (state.inputValue.length > 2) {
                                        setOpenOptions(true)
                                        return options.filter((item) =>
                                            String(item).toLowerCase().includes(state.inputValue.toLowerCase())
                                        );
                                    } else {
                                        setOpenOptions(false)
                                        return [];
                                    }
                                }}
                                renderInput={(params) => (
                                    <TextField
                                    {...params}
                                    label="Descrizione"
                                    id="newDesc"
                                    name="newDesc"
                                    onBlur={() => setOpenOptions(false)}
                                    onChange={(e) => props.setDesc(e.target.value)}
                                    InputProps={{
                                        ...params.InputProps,
                                        type: 'search'
                                    }}
                                    />
                                )}
                                />
                            </Grid>
                            <Grid xs={12} md={2} item>                   
                                <FormControl sx={{minWidth: 150 }}>
                                    <InputLabel id="demo-simple-select-autowidth-label">Unità di misura</InputLabel>
                                    <Select
                                    labelId="demo-simple-select-autowidth-label"
                                    id="newUm"
                                    name="newUm"
                                    value={props.um}
                                    onChange={(e) => props.setUm(e.target.value)}
                                    autoWidth
                                    label="Unità di misura"
                                    >
                                    <MenuItem value={"ac"}>A corpo</MenuItem>
                                    <MenuItem value={"mq"}>Metri quadrati</MenuItem>
                                    <MenuItem value={"ml"}>Metri lineari</MenuItem>
                                    </Select>
                                </FormControl>
                            </Grid>
                            <Grid xs={12} md={2} item>                   
                                <FormControl>
                                    <InputLabel htmlFor="outlined-adornment-amount">Quantità</InputLabel>
                                    <OutlinedInput
                                        id="newQt"
                                        name="newQt"
                                        type="number"
                                        value={props.qt}
                                        onChange={(e) => props.setQt(e.target.value)}
                                        startAdornment={<InputAdornment position="start"></InputAdornment>}
                                        label="Amount"
                                    />
                                </FormControl>
                            </Grid>
                            <Grid xs={12} md={2} item>                   
                            <FormControl>
                                <InputLabel htmlFor="outlined-adornment-amount">Prezzo unitario</InputLabel>
                                <OutlinedInput
                                    id="newPrezzo"
                                    name="newPrezzo"
                                    type="number"
                                    value={props.prezzo}
                                    onChange={(e) => props.setPrezzo(e.target.value)}
                                    startAdornment={<InputAdornment position="start">€</InputAdornment>}
                                    label="Amount"
                                />
                            </FormControl>
                            </Grid>
                            <Grid xs={12} md={3} item> 
                            </Grid>
                            <Grid xs={12} md={3} item>   
                            { props.editing ?
                            <Box textAlign='center' sx={{mt:1}}>
                                <Button variant='contained' type="submit" onClick={handleModify}>
                                    Modifica prodotto
                                </Button>
                            </Box>
                            :
                            <Box textAlign='center' sx={{mt:1}}>
                                    <Button variant='contained' type="submit" onClick={handleSubmit}>
                                        Aggiungi prodotto
                                    </Button>
                                </Box>
                            }                
                            </Grid>
                        </Grid>
                    </Box>
                </Modal>
            </form>
            
        )
    }