import { Button, ButtonGroup, Fab, Grid, Typography } from "@mui/material";
import React, { useRef, useState } from "react";
import AddIcon from '@mui/icons-material/Add';
import NuovoProdotto from "./NuovoProdotto";
import InviaMail from "./InviaMail";
import ConfermaNuovo from "./ConfermaNuovo";

export default function Pulsantiera(props) {

    const [openMail, setOpenMail] = React.useState(false);
    const [openNew, setOpenNew] = React.useState(false);


        return (
            <Grid container spacing={2} padding={5}>
                
                <Grid xs={12} item align={"center"}>
                    <ButtonGroup variant="outlined" size="large" aria-label="outlined primary button group">
                        <Button color="success" type="submit">Salva</Button>
                        <Button disabled={props.disabled} id="stampa" color="info" target='_blank' href={window.location.protocol + '//' + window.location.host +"/api/preventivi/"+props.getId+"/stampa"}>Stampa</Button>
                        <Button disabled={props.disabled} id="invia_mail" color="warning" onClick={() => setOpenMail(true)}>Invia mail</Button>
                    </ButtonGroup>
                    <InviaMail openMail={openMail} closeMail={() => setOpenMail(false)} getId={props.getId}></InviaMail>
                </Grid>
                <Grid xs={12} item align={"center"} sx={{display:props.nuovo ? 'block' : 'none'}}>
                    <ButtonGroup variant="outlined" size="large" aria-label="outlined primary button group">
                        <Button onClick={() => setOpenNew(true)} color="primary">Crea nuovo preventivo</Button>
                    </ButtonGroup>
                    <ConfermaNuovo open={openNew} close={() => setOpenNew(false)}/>
                </Grid>           
            </Grid>
            
        )

}
