import { Box, Card, Grid, Icon, TextField, Typography } from "@mui/material";
import React, { useState } from "react";

export default function Cliente(props) {

        return (
            <Grid container spacing={2} padding={5}>
                
                <Grid xs={12} item>
                    <Typography sx={{textAlign: "center" }} variant="h5" color="text.secondary" gutterBottom>
                        Dati Cliente
                    </Typography>
                    
                </Grid>
                <Grid xs={6} item>                   
                    <TextField sx={{mb: 2}} focused={props.datiCliente ? true : false} fullWidth id="nome" label="Nome" value={props.datiCliente ? props.datiCliente.nome : null} onChange={(e) => props.setNome(e.target.value)} variant="standard" required/>
                </Grid>
                <Grid xs={6} item>
                    <TextField sx={{mb: 2}} focused={props.datiCliente ? true : false} fullWidth id="cognome" label="Cognome" value={props.datiCliente ? props.datiCliente.cognome : null} onChange={(e) => props.setCognome(e.target.value)} variant="standard" required/>
                </Grid>
                <Grid xs={12} item>
                    <TextField sx={{mb: 2}} focused={props.datiCliente ? true : false} fullWidth id="indirizzo" label="Indirizzo" value={props.datiCliente ? props.datiCliente.indirizzo : null} onChange={(e) => props.setIndirizzo(e.target.value)} variant="standard" required/>
                </Grid>
                
            </Grid>
            
        );
}